import React from 'react'

import Layout from '../components/category/Layout'
import { graphql } from 'gatsby';

const CategoryPage = () => {
    return (
        <Layout>
            <h2>Hello</h2>
        </Layout>
    )
}

export const query = graphql`
    query getAllPostsWithCategory {
        allMarkdownRemark {
            edges {
                node {
                    id
                }
            }
        }
    }
`

export default CategoryPage